import React from 'react'
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import Offcanvas from 'react-bootstrap/Offcanvas';
import NavDropdown from 'react-bootstrap/NavDropdown';
import navlogo from "../Images/navlogo.png"
import logo from "../Images/Logo.png"
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';

function Header() {
    return (
                         <div>
<Navbar collapseOnSelect expand='lg'  data-bs-theme="dark" fixed='top'>
        
        <Navbar.Brand href="#" className='mx-4'>
        <div>
        <img src={logo}
                width={200}
                height={50}/>
        </div>
      
        </Navbar.Brand>
         <Navbar.Toggle aria-controls="offcanvasNavbar-expand"/>
      <Navbar.Offcanvas id="offcanvasNavbar-expand" aria-labelledby="offcanvasNavbarLabel-expand" placement="end" className="canvas-1">
            <Offcanvas.Header closeButton>
                <Offcanvas.Title id="offcanvasNavbarLabel-expand">
                    <img src={logo}
                        width={200}
                        height={50}/>
                </Offcanvas.Title>
            </Offcanvas.Header>
            <Offcanvas.Body className='justify-content-center'>
                <Nav className="justify-content-center align-items-center flex-grow- pe-0">
                    <Nav.Link href="#action1" className='mx-2 my-2'>Home</Nav.Link>
                    <NavDropdown title="Web3" id="navbarScrollingDropdown" className='mx-2 my-2'>
                        <NavDropdown.Item href="#action3">Action</NavDropdown.Item>


                    </NavDropdown>
                    <NavDropdown title="Metaverse" id="navbarScrollingDropdown" className='mx-2 my-2'>
                        <NavDropdown.Item href="#action3">Action</NavDropdown.Item>


                    </NavDropdown>
                    <NavDropdown title="Crypto" id="navbarScrollingDropdown" className='mx-2 my-2'>
                        <NavDropdown.Item href="#action3">Action</NavDropdown.Item>


                    </NavDropdown>
                    <NavDropdown title="NFT" id="navbarScrollingDropdown" className='mx-2 my-2'>
                        <NavDropdown.Item href="#action3">Action</NavDropdown.Item>

                    </NavDropdown>
                    <Nav.Link href="#action3" className='mx-2 my-2'>Blog</Nav.Link>
                    <Nav.Link href="#action4" className='mx-2 my-2'>About</Nav.Link>
                    <Nav.Link href="#action">
                        <img src={navlogo}
                            width={110}/>
                    </Nav.Link>


                </Nav>

            </Offcanvas.Body>

        </Navbar.Offcanvas>  
  
</Navbar>
                     
                        </div>


                       
    )
}

export default Header
 

