import React from 'react'
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { Container } from 'react-bootstrap';
import Button from 'react-bootstrap/Button';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import whatsapp from "..//Images/whatsapp-line.png"
import tele from "..//Images/telegram-line.png"
import phone from "..//Images/phone-fill.png";


function Middle() {
  return (
    <div>
                                  {/*Main Conten*/}

<div className='m1-img-1'>
  <Container >
  <Row >
  <Col  lg={6} sm={10} md={12} xl={6} xs={12}>
   <div className='m1-col-1'    >
   Cryptocurrency 
   Wallet Development Company
   </div>
</Col>
<Row>
<Col lg={6} sm={12} md={12} xl={6} xs={12}>
   <div className='m1-col-2'>
   Launch a premier crypto wallet with Dappfort, crypto wallet app developers that supports multiple cryptocurrencies, stablecoins, tokens, NFTs, etc. As a top cryptocurrency wallet development company, our cryptocurrency wallets have cutting-edge payment integration technologies that deliver error-free transaction capabilities.
   </div>
   </Col>
</Row>
<Row>
<Col  lg={12} sm={6} md={12} xl={12} xs={12}>
   <div  className='m1-col-3' >
   <Button id="connect-whatsapp-1" className='m-btn-1  mx-2' size="lg" variant="primary">Connect whatsapp</Button>
   
   <Button id="connect-whatsapp-2" className=' m-btn-1 my-4 mx-2' size="lg" variant="outline-light">Connect Telegram</Button>
   </div>
   </Col>
</Row>

  
  
   
  
 
  
 <ButtonGroup className='btn-group' id='connect-whatsapp-1'   vertical fixed="left" >
 <a href='https://google.com' ><img  href="https://google.com" className='btn-grp-1 my-2 mx-1' src={whatsapp} /></a>
 <a href='https://yahoo.com' ><img  className='btn-grp-1 my-2 mx-1' src={tele} /></a>
 <a href='https://google.com' ><img  className='btn-grp-1 my-2 mx-1' src={phone} /></a> 
 </ButtonGroup>
   </Row>
  
   
  </Container>
 
  
   </div>

                                  {/*Content Two*/}


 
                                  {/*Content Three*/}
   
    </div>
   
   
  )
}

export default Middle



